	import common from '../../common/common.js';
	export default {
		data() {
			return {
	        loginForm: {
					loginName: '1',
					password: '1'
				}
			}
		},
		onLoad() {

		},
		methods: {
			login() {
				uni.request({
					url: common.webUrl + 'user/getUser',
					data: this.loginForm,
					header: {
						'custom-header': 'hello' //自定义请求头信息
					},
					method: 'POST',
					dataType: 'json',
					success: (res) => {
						if (res.data.resCode == '0000' && res.data.data.length > 0) {
							uni.showToast({
								title:"登陆成功"
							})

                            var userObj = res.data.data[0];
							uni.setStorage({
								key: 'userObj',
								data: userObj,
								success: function () {
									console.log(JSON.stringify(userObj));
								}
							});
	                     /*跳转新路径*/
						 // uni.navigateTo({ //跳转到非TAB页面
						 //     url: '../index/index'
						 // });
						 uni.switchTab({ //跳转到TAB页面的某个页面
						     url: '/pages/home/home'
						 });
						} else {
							// plus.nativeUI.alert("登陆失败");
							uni.showToast({
								title:"登陆失败"
							})
						}
					}
				});
			},
			wexinLogin:function(res){
				    if (!res.detail.iv) {
				        uni.showToast({
				            title: "您取消了授权,登录失败",
				            icon: "none"
				        });
				        return false;
				    }
					console.log('-------用户授权，并获取用户基本信息和加密数据------')
					console.log(res.detail.userInfo);
					this.loginForm = {
						loginName: res.detail.userInfo.nickName,
						password: '111111'
					}
			        uni.request({
			        	url: common.webUrl + 'user/weixinUserLogin',
			        	data: {
							"loginName":this.loginForm.loginName,
							"photo":res.detail.avatarUrl,
							"password":"111111",
							"name":this.loginForm.loginName
						},
			        	method: 'POST',
			        	dataType: 'json',
			        	success: (res) => {
			        		if (res.data.resCode == '0000') {
								   this.login()
			        		}
			        	}
			        });
			
			}
		}
	}
